.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  background: rgba(15, 15, 15, 0.3);
}

.menu {
  display: flex;
  align-items: center;
  padding: 20px 0;

  &__list {
    display: flex;
    margin-right: auto;
    font-size: 20px;
    line-height: 30px;

    li {
      + li {
        margin-left: 55px;
      }
    }

    a {
      background: -webkit-linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
      background-clip: text;
      -webkit-background-clip:text;
      color:rgba(255,255,255,1);
      display:block;
      transition: color .2s;
      &:hover {
        color:rgba(255,255,255,0);
      }
    }
  }

  &__social {
    display: flex;
    margin-right: 50px;

    li {
      position: relative;
      + li {
        margin-left: 25px;
      }
    }
    a {
      display: block;
      position: relative;
      width: 24px;
      height: 24px;
      &:hover {
        .svg-main {
          opacity: 0;
        }
        .svg-color {
          opacity: 1;
        }
      }
    }

    .svg-main,
    .svg-color {
      transition: opacity 0.2s;
      position: absolute;
      width: 100%;
      height: 100%;
    }
    .svg-color {
      opacity: 0;
    }

    svg {
      &:hover {
        path {
          fill: linear-gradient(90.71deg, #8D5BDF 0%, #1860ED 100%);
        }
      }
    }
  }

  .logo {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 100px;
    width: 180px;
    height: 54px;
  }
}
